declare function require(moduleName: string): any;
import * as React from "react";

const Menu = require('react-data-grid-addons').Menu;
const ContextMenu = Menu.ContextMenu;
const MenuItem = Menu.MenuItem;

export interface MyContextMenuProps {
  onRowDelete: Function,
  onRowInsertAbove: Function,
  onRowInsertBelow: Function,
  rowIdx: string,
  idx: string
}


export default class MyContextMenu extends React.Component<MyContextMenuProps, undefined> {

  constructor(props: MyContextMenuProps) {
    super(props);
  }


  onRowDelete(e: any, data: any) {
    this.props.onRowDelete(e, data);
  }

  onRowInsertAbove(e: any, data: any) {
    this.props.onRowInsertAbove(e, data);
  }

  onRowInsertBelow(e: any, data: any) {
    this.props.onRowInsertBelow(e, data);
  }

  render() {
    return (
      <ContextMenu>
        <MenuItem data={{rowIdx: this.props.rowIdx, idx: this.props.idx}} onClick={()=>this.onRowDelete}>Delete
          Row</MenuItem>
      </ContextMenu>
    );
  }

}



